.breed-select {
  font-size: 22px;
  display: none;
}

.label {
  font-size: 30px;
}

.loader {
  width: 64px;
  height: 64px;
  background: #09f15e;
  border-radius: 50%;
  animation: rotate 2s linear infinite;
  display: block;
  position: relative;
  transform: rotate(45deg);
}

.loader:before {
  content: "";
  width: 15px;
  height: 30px;
  background: #ff3d00;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: skew(5deg, 60deg)translate(-50%, -5%);
}

.loader:after {
  content: "";
  width: 6px;
  height: 6px;
  background: #fff;
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@keyframes rotate {
  0% {
    transform: rotate(45deg);
  }

  30%, 50%, 70% {
    transform: rotate(230deg);
  }

  40%, 60%, 80% {
    transform: rotate(240deg);
  }

  100% {
    transform: rotate(245deg);
  }
}

.error {
  display: none;
}

.cat-info {
  flex-wrap: wrap;
  font-size: 22px;
  display: flex;
}

.cat-info img {
  max-width: 600px;
  margin-top: 20px;
  margin-right: 20px;
}

.cat-description {
  max-width: 600px;
}

